import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

export const primaryBright = '#fff';
export const secondaryBright='#e8e8e8';
export const accentMain='#750000';
export const tint= '#8c8c8c';
export const successBlue= '#66a7b8';
export const failureRed= '#f19898ab';
export const shadow= '#383838';
export const primaryDark= '#1a1b1f';
export const appBlack = '#2a2a2a';
export const smoke=  '#2423239d';
export const cream = '#c4bebc'


const useStyles = makeStyles(theme => ({
    root: {
        
        minHeight: '100vh',
        minWidth:'520px',
        flexGrow: 1,
        backgroundColor: appBlack,
        padding:'4px',
    },
   
    
    alertSuccess:{
        justifySelf:'center',
       
        color: appBlack,
        padding:'5px',
        backgroundColor: successBlue,
        height: '30px',
        fontSize:'15px',
        borderRadius:'8px'
    },
    alertFailure:{
        justifySelf:'center',
        
        color: shadow,
        padding:'5px',
        backgroundColor: failureRed,
        height: '30px',
        fontSize:'15px',
        borderRadius:'8px',
        
    },
    backdrop:{
        zIndex: theme.zIndex.drawer + 1,
        color: primaryBright,
        
    },
    backdropCircle:{
        zIndex: theme.zIndex.drawer + 1,
        color: accentMain,
        opacity:'20%'
    },

    belowNavBar:{
    
        marginTop:'70px',
        minHeight: '80vh',
        minWidth:'520px',
        backgroundColor:appBlack
    },
    belowNavBarBody:{
        minHeight: '80vh',
        minWidth:'520px',
        backgroundColor:appBlack
    },
    chartHolder:{
        backgroundColor:appBlack,
        borderStyle:'ridge',
        borderColor: tint,
        borderWidth:'1px',
        borderRadius:'8px',
        padding:'8px',
        margin:'4px',
        color:primaryBright
    },

    divided:{
        borderStyle:'solid',
        borderColor: primaryBright,
        borderWidth: '5px',
        height:'5px',
        
    },
    greeting:{
        position:'fixed',
        right:'80px',
        top:'38px',
        fontSize:'15px',
        color:secondaryBright,
        opacity:'70%'

    },
    loginForm:{    
        display: 'Grid', 
        gridTemplateColumns: '1fr',
        backgroundColor: appBlack,
        gridGap:'1em',
        
        
        
    },
    loginScreen:{
        alignContent:'center',
        justifyContent:'center',
        borderStyle:'',
        borderWidth: '20px',
        borderRadius:'10%',
        borderColor: tint,
        backgroundColor: appBlack,
        display: 'flex',
       
        
        
    },
    logo:{
        display:'flex',
        alignSelf:'center',
        justifySelf:'center',
        width:'100px',
        height:'100px',
        borderRadius:'50%',
        opacity:'5%'
    },
    logoSmall:{
        position:'fixed',
        right:'48px',
        marginTop:'2px',
        width:'24px',
        height:'24px',
        borderRadius:'50%',
        opacity:'70%'
       
    },
    paper:{
        backgroundColor: appBlack,
        padding:'8px', 
        textAlign:"center",
        color: primaryBright,
        borderColor: tint,
        borderStyle: "outset",
        borderWidth:"1px",
        
    },
    paperFlatButton:{
        position: 'absolute',
        color:primaryBright,
        backgroundColor: appBlack,
        borderWidth: '1px',
        borderRadius: '4px',
        padding: '4px 10px',
        fontSize: '15px',
    },
    paperSelect:{
        color:primaryDark,
        backgroundColor: primaryBright,
        borderWidth: '1px',
        borderRadius: '4px',
        padding: '4px 10px',
        fontSize: '14px',
        
    },
    paperSignInButton:{
        display:'flex',
        justifySelf:'center',
        justifyContent:'center',
        color:secondaryBright,
        backgroundColor: appBlack,
        borderWidth: '1px',
        borderRadius: '4px',
        borderColor: primaryBright,
        width: '90px',
        padding: '4px 16px',
        fontSize: '15px',
        textAlign: 'center',
        fontWeight: 'bold',
        fontSize: '20px'
        

    },
    placeHolderColor:{
        backgroundColor:primaryDark
    },
    qaForm_InfoLabel:{
        fontSize:'14px',
        justifySelf:'center',
        padding: '4px 10px',
    },
    qaForm_Case:{
        
        backgroundColor:cream,
        borderColor:appBlack,
        borderWidth:'5px',
        borderStyle:'ridge',
        padding:'8px'
    },
    qaForm_Score:{
        float:'left',
        width:'120px',
        backgroundColor:shadow,
        borderColor:appBlack,
        borderWidth:'5px',
        borderStyle:'ridge',
        borderRadius:'8px',
        padding:'4px',
        fontSize:'20px',
        fontWeight:'bold',
        color:primaryBright,
        margin:'8px'
        

    },
    qaPaperFlatButton:{
        float:'right',
        color:primaryBright,
        backgroundColor: appBlack,
        borderStyle:'ridge',
        borderColor:tint,
        borderWidth: '2px',
        borderRadius: '8px',
        padding: '10px',
        margin:'5px',
        fontSize: '20px',
    },
    qaPaperFlatButtonSubmitting:{
        float:'right',
        color:primaryDark,
        backgroundColor: primaryBright,
        borderStyle:'ridge',
        borderColor:appBlack,
        borderWidth: '2px',
        borderRadius: '4px',
        padding: '10px',
        margin:'5px',
        fontSize: '20px',
    },
    qaForm_Header:{
        
        borderBottomColor:'black',
        borderBottomWidth:'2px',
        borderBottomStyle:'solid',
        
    },
    qaForm_Label:{
        fontSize:'16px',
        padding:'10px'
    },
    qaForm_paperSelect:{
        
        color:primaryDark,
        backgroundColor: primaryBright,
        borderWidth: '1px',
        borderRadius: '4px',
        padding: '2px 5px',
        fontSize: '14px',
        
    },
    qaForm_comment:{
        width:'98%',
        height:'100px',
        color:primaryDark,
        backgroundColor: primaryBright,
        borderWidth: '1px',
        borderRadius: '4px',
        padding: '2px 5px',
        fontSize: '14px',
        margin:'8px',
        fontSize:'18px',
       
    },
    
    qaScoreCard:{
        fontSize:'15px',
        borderBottomStyle:'ridge',
        borderLeftStyle:'ridge',
        borderColor: tint,
        borderWidth:'1px',
        borderLeftWidth:'2px',
        padding:'4px 10px',
        margin:'4px',
        borderRadius:'8px',
        color:primaryBright,
        backgroundColor:shadow
    },
    qaScoreTable:{
        backgroundColor:appBlack,
        borderBottomStyle:'ridge',
        borderLeftStyle:'ridge',
        borderColor: tint,
        borderWidth:'1px',
        borderLeftWidth:'2px',
        borderRadius:'8px',
        padding:'4px',
        color:primaryBright
    },
    qaScoreCard_score:{
        margin:'3px',
        padding:'4px',
        borderStyle:'ridge',
        borderColor:  accentMain,
        borderWidth:'1px',
        borderRadius:'15px',
        fontSize:'15px',
        fontWeight:'bold',
        color: tint,
        backgroundColor:appBlack,
        
    },
    qaScoreCard_comment:{
        
        borderTopStyle:'solid',
        borderColor: tint,
        borderWidth:'1px',
        display:'absolute',
        color: secondaryBright,
        fontSize:'16px',
        
        
        // float:'right'
    },
    qaScoreCard_entryDate:{
        display:'absolute',
        // float:'right'
        color: tint,
   
    },
    qaScoreCard_caseNum:{
        display:'absolute',
        color: tint,
        
       
        
   
    },
    reportLeftGrid:{
        
        width:'100vw',
        backgroundColor:appBlack,
        height:'100vh',
        // border:'groove 1px #000'
    },
    reportRightGrid:{
        width:'100vw',
        backgroundColor:appBlack,
        height:'100vh',
        // border:'groove 1px #000',
    },
    statReportTitle:{
        justifySelf:'center',
        fontSize:'16px',
        borderTopStyle:'ridge',
        borderLeftStyle:'ridge',
        borderWidth:'1px',
        padding:'2px 8px',
        borderRadius:'4px',
        color:primaryBright,
        backgroundColor:appBlack,
        
    },
    
    statCard:{
        fontSize:'13px',
        borderStyle:'ridge',
        borderColor: tint,
        borderWidth:'1px',
        padding:'5px 5px',
        // margin:'2px',
        borderRadius:'8px',
        color:primaryBright,
        backgroundColor:shadow
    },
    statCard_Selected:{
        marginLeft:'3px',
        fontSize:'13px',
        borderStyle:'ridge',
        borderColor: primaryBright,
        borderWidth:'1px',
        padding:'5px 8px',
        // margin:'2px',
        borderRadius:'8px',
        color:primaryBright,
        backgroundColor:smoke
    },
    statCard_button:{
        margin:'2px',
        width:'100%',
        padding:'2px',
        borderStyle:'ridge',
        borderColor: tint,
        borderWidth:'1px',
        borderRadius:'8px',
        borderTopWitdh:'0px',
        
    },
    statCard_category:{
        minWidth:'70%',
        // alignContent:'center',
        justifySelf:'center',
        fontSize:'16px',
        borderBottomStyle:'ridge',
        borderRightStyle:'ridge',
        borderWidth:'1px',
        padding:'2px 8px',
        borderRadius:'4px',
        color:primaryBright,
        backgroundColor:appBlack
    },
    statCard_total:{
        marginLeft:'5px',
        // float:'right'
        color: secondaryBright,
        fontSize:'22px',
        fontWeight:'bold',
        
        
        padding:'1px'
        
        
    },
    statCard_teamCompare:{
        display:'absolute',
        padding:'5px 0px 0px 5px',
        fontSize:'15px',
        
        color: tint,
    },

    statHolder:{
        backgroundColor:appBlack,
        borderStyle:'ridge',
        borderColor: tint,
        borderWidth:'1px',
        borderRadius:'8px',
        padding:'8px',
        color:primaryBright
    },
    


    title:{
        color: primaryBright,
        marginLeft: '5px',
       
        
    },
    inputs:{
        color: appBlack,
        padding:'5px',
        backgroundColor: secondaryBright,
        height: '30px',
        fontSize:'18px',
        borderRadius:'8px'
    }
    
}));

export default useStyles;