export const techStatsHelper =(data,teamData)=>{
    // console.log(data)
    
    const techName = data.tech
    const callsTaken = data.callsTaken
    const missedCalls = data.missedCalls
    const casesOpened = data.sn_openCase
    const tasksOpened = data.sn_openTask
    const totalOpened = data.sn_openTotal
    const casesClosed = data.sn_closedCase
    const heroPoints = data.heroPoints
    const inQueue = data.inQueue
    const paused = data.paused
    const pausedPercent = data.paused_percent
    const sla = data.sla_Met
    
    
    const categoryNames = [
        ['Calls Taken',callsTaken],
        ['Missed Calls', missedCalls],
        ['Cases Opened',casesOpened], 
        ['Task Opened',tasksOpened], 
        ['Total Opened',totalOpened], 
        ['Closed Cases',casesClosed],
        ['Hero Points',heroPoints], 
        ['SLA',sla]
		
        // ['In Queue',inQueue], 
        // ['Paused',paused],
        // ['Paused %',pausedPercent]
    ]

    return (categoryNames)
}
export const slaCalc =(slaMet,slaNotMet)=>{
	var total = slaMet + slaNotMet;
	var slaMetPercent = ((slaMet / total) * 100).toFixed(2)
	
	return [slaMetPercent.toString(),total]
}
export const totalCalc = (tech,team)=>{
	var team_ct = []
	var team_mc = []
	var team_co = []
	var team_to = []
	var team_tto = []
	var team_cc = []
	var team_hp = []
    // console.log(team)

	for(const i in team){
		if(team[i].callsTaken > 0){
			team_ct.push(team[i].callsTaken)
		}
	};
	for(const i in team){
		if(team[i].missedCalls > 0){
			team_mc.push(team[i].missedCalls)
		}
	};
	for(const i in team){
		if(team[i].sn_openCase > 0){
			team_co.push(team[i].sn_openCase)
		}
	};
	for(const i in team){
		if(team[i].sn_openTask > 0){
			team_to.push(team[i].sn_openTask)
		}
	};
	for(const i in team){
		if(team[i].sn_openTotal > 0){
			team_tto.push(team[i].sn_openTotal)
		}
	};
	for(const i in team){
		if(team[i].sn_closedCase > 0){
			team_cc.push(team[i].sn_closedCase)
		}
	};
	for(const i in team){
		if(team[i].heroPoints > 0){
			team_hp.push(team[i].heroPoints)
		}
	};
	const ct_total = (team_ct.reduce((a, b) => a + b, 0))
	const mc_total = (team_mc.reduce((a, b) => a + b, 0))
	const co_total = (team_co.reduce((a, b) => a + b, 0))
	const to_total = (team_to.reduce((a, b) => a + b, 0))
	const tto_total = (team_tto.reduce((a, b) => a + b, 0))
	const cc_total = (team_cc.reduce((a, b) => a + b, 0))
	const hp_total = (team_hp.reduce((a, b) => a + b, 0))
	
	const tech_ctPerc = ((tech.callsTaken / ct_total) * 100).toFixed(2)
	const tech_mcPerc = ((tech.missedCalls / mc_total) * 100).toFixed(2)
	const tech_coPerc = ((tech.sn_openCase / co_total) * 100).toFixed(2)
	const tech_toPerc = ((tech.sn_openTask / to_total) * 100).toFixed(2)
	const tech_ttoPerc = ((tech.sn_openTotal / tto_total) * 100).toFixed(2)
	const tech_ccPerc = ((tech.sn_closedCase / cc_total) * 100).toFixed(2)
	const tech_hpPerc = ((tech.heroPoints / hp_total) * 100).toFixed(2)
    
	// returns [sla_percent met, sla total met+not met]
    const tech_sla = slaCalc(tech.sla_Met, tech.sla_NotMet)
    
	const team_compare_message = [
		'team',
		'team',
		'team',
		'team',
		'team',
		'team',
		'team',
		'personal'
	]
	

	return [
		[team_compare_message[0],tech_ctPerc,ct_total],
		[team_compare_message[1],tech_mcPerc,mc_total],
		[team_compare_message[2],tech_coPerc,co_total],
		[team_compare_message[3],tech_toPerc,to_total,],
		[team_compare_message[4],tech_ttoPerc,tto_total],
		[team_compare_message[5],tech_ccPerc,cc_total],
		[team_compare_message[6],tech_hpPerc,hp_total],
		[team_compare_message[7],tech_sla[0],tech_sla[1]]
	];
}
export const buildBarColors =(prepData,tech,tech_data,cat_,top_color,bottom_color)=>{
	var barColors = []
	
	const testColor = '#fff'
	for(const i in prepData[0]){
		if(prepData[1][i] === tech){
			barColors.push('#575757')
		}else{
			if(cat_ === 'missedCalls'){
				if (prepData[0][i] > tech_data[cat_]){
					barColors.push(bottom_color)
				}
				else if (prepData[0][i] === tech_data[cat_]){
					barColors.push(tint)
				}
				else{
					barColors.push(top_color)
				}
			}else{
				if (prepData[0][i] > tech_data[cat_]){
					barColors.push(top_color)
				}
				else if (prepData[0][i] === tech_data[cat_]){
					barColors.push(tint)
				}
				else{
					barColors.push(bottom_color)
				}
			}
		}
	}
	return(barColors)
}






