import axios from 'axios';
import {useDispatch} from 'react-redux'



export const LogoutSession = () => async dispatch =>{
    dispatch({
        type: 'LOG_OUT',
        
    })
}

export const SessionAuth = (sesKey) => async dispatch => {
    // console.log(sesKey,'sessionkey exist so trying to auth usuing it')
    try{
        
        dispatch({
            type: 'AUTHORIZING_SESSION',
            sesKey: sesKey
        })
        const res =  await axios.get('https://fb-loginauth.azurewebsites.net/api/fb-loginAuth?SessionKey='+sesKey)
        
        
        dispatch({
            type: 'SESSION_AUTHORIZED',
            payload: res.data,
            sesKey: sesKey
        })

    } catch(e){
        dispatch({
            type: 'SESSION_FAIL',
            sesKey: 'SESSION_FAILED'
        })
    }
};


export const LoginAuth = (email,password,AuthType) => async dispatch => {
    dispatch({
        type: 'AUTHORIZE_LOADING'
    })
    try {
        
        var res = ''
        if(AuthType === 'Login'){
    
            res = await axios.get('https://fb-loginauth.azurewebsites.net/api/fb-loginAuth?email='+email+'&password='+password)
          
            dispatch({
                type: 'AUTHORIZE_SUCCESS_LOGIN',
                payload: res.data
            })   
        }
        if(AuthType === 'Register'){
            res = await axios.get('https://fb-loginauth.azurewebsites.net/api/fb-loginAuth?email='+email+'&rg='+password)
            dispatch({
                type: 'AUTHORIZE_SUCCESS_REGISTER',
                payload: res.data
            })
        }
        
    } catch (e){
        console.log(e)
        dispatch({
            type: 'AUTHORIZE_FAIL'
        })
    }
};

export const GetEmployeeList = () => async dispatch => {
    try {
        dispatch({
            type: 'EMPLOYEE_LIST_LOADING'
        })
        const res = await axios.get('https://qaimporthttp.azurewebsites.net/api/getEmpList')
        

        dispatch({
            type: 'EMPLOYEE_LIST_SUCCESS',
            payload: res.data
            
        })
    } catch (e){
        dispatch({
            type: 'EMPLOYEE_LIST_FAIL',
        })

    }
    
};