import React, { useReducer, useState, useContext, useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux'
import Grid from '@material-ui/core/Grid'
import Switch from '@material-ui/core/Switch'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import * as FaIcons from 'react-icons/fa';
import * as FiIcons from 'react-icons/fi';
import * as AiIcons from 'react-icons/ai';
import { Link } from 'react-router-dom';
import { SidebarData} from './SidebarData';
import './Navbar.css';
import useStyles from '../styles'
import { IconContext} from 'react-icons';
import techSupportLogo from '../images/techSupportLogo.png';
import {LogoutSession} from '../actions/EmployeeActions';





function Navbar(){
    const state = useSelector(state => state)
    
    const categorySortedData = state.ChartPull.categorySortedData
    const category = state.ChartPull.category
    const classes = useStyles();
    const [showMarquee, setShowMarquee] = useState(true)
    const [cached_email, setCachedEmail] = useState('');
    if(cached_email === ''){
        if(localStorage.getItem('cached_email')){setCachedEmail(localStorage.getItem('cached_email'))}
    }
    // console.log(cached_email)
    const dispatch = useDispatch();
    const [sidebar, setSidebar] = useState(false)
    const showSidebar = () => setSidebar(!sidebar)

    const userInfo = useSelector(state => state.UserAuth);
    const userEmail = userInfo.userEmail;
    const userName = userInfo.userName;
    const userAccessLevel = userInfo.userAccessLevel;
    const message = userInfo.msg;
    var firstname = ''
    if(userName){
       firstname = userName.split(" ")[0]
    }
    

    const Logout = () => {
                        
        localStorage.removeItem('session_key'); 
        dispatch(LogoutSession())
        // window.location.assign('/');
        
        // if(cached_email !== ''){
        //     localStorage.setItem('cached_email',cached_email)
        // }
    }
    
    const createFeed = ()=>{
        const test = [
            {number:" this is still broken as it goes off the screen",tag:"Moe's Cinco Promo"},
            {number:'',tag:'Mcal Price Update'},
            {number:'3',tag:'Hello from 3'},
            {number:'4',tag:'Hello from 4'},
            {number:'5',tag:'Hello from 5'},
            {number:'6',tag:'Hello from 6'},
            {number:'7',tag:'Hello from 7'},
            {number:'8',tag:'Hello from 8'},
            {number:'9',tag:'Hello from 9'},     
        ]
        return(
            <>
                {test.map((tech,index)=>{
                    return( 
                            <span key={index+1} className={'message'}>
                                <span className={'message-case'}>
                                <span className={'message-number'}>{tech.number}</span>
                                <>{tech.tag}</>
                                </span>
                            </span>
                        )    
                    }
                )}
            </>   
        ) 
        }
    


    return (
            <>
            <IconContext.Provider value={{color: '#fff'}}>
                <div className="navbar">
                    <Link to="#" className='menu-bars'>
                        <FiIcons.FiMenu onClick={showSidebar}/>
                    </Link>
                    
                </div>
                
                <span className='version'> v.2.0 </span>
                <span className='headingTitle'>Tech Dashboard</span>
                <span className= {classes.greeting}>Welcome back{', '+firstname}!</span>
                <button className={'flatButton'} style={{top:'34px',right:'3px'}} onClick={Logout}>Logout</button>
               
               
                {/* <button className={'flatButton'} style={{top:'34px',right:'300px'}} onClick={()=>{dispatch({type: 'login',user:'Steven',loggedin:true})}}>Hide News</button>
         */}    
                {/* <FormControlLabel control={<Switch size="small" checked={!showMarquee} onClick={console.log('hello')} />} label="Small"/> */}

                <img src={techSupportLogo} className={classes.logoSmall}/>
                
                <nav className={sidebar ? 'nav-menu active' : 'nav-menu'}>
                
                    <ul className='nav-menu-items'  onClick={showSidebar}>
                        <li className="navbar-toggle">
                            <Link to='#' className='menu-barX'>
                                <AiIcons.AiOutlineClose/>
                            </Link>
                        </li>
                        {SidebarData[userAccessLevel ? userAccessLevel : 1].map((item, index) =>{
                            return(
                                <li key={index} className={item.cName}>
                                    <Link to={item.path}>
                                        {item.icon}
                                        <span>{item.title}</span>
                                    </Link>
                                </li>
                            );
                        })}
                    </ul>
                </nav>
            </IconContext.Provider>
        </>
    )
}

export default Navbar