import React, {useState, useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux';
import _ from 'lodash';
import useStyles, { accentMain, primaryBright, tint } from '../styles';
import { Bar, Line} from 'react-chartjs-2';
import {buildBarColors} from '../helperFunctions/helperTechStats'



export const CategoryChart =()=>{
    const classes = useStyles();
    const state = useSelector(state => state)
  
    const categorySortedData = state.ChartPull.categorySortedData
    const category = state.ChartPull.category
    const categoryTerm = state.ChartPull.categoryTerm
    const access_level = state.UserAuth.userAccessLevel
    const selectedTech = state.ChartPull.selectedTech
    const selectedTechData = state.ChartPull.chartData[0]
    const month = state.ChartPull.month;
    const [filter, setFilter] = useState(0) 
    const access_filter = accessDataFilter(categorySortedData,access_level,filter,selectedTech)
    const prepData = graphPrep(access_filter,selectedTech,categoryTerm)
    const barColors = buildBarColors(prepData,selectedTech,selectedTechData,categoryTerm,accentMain,primaryBright)

    useEffect(()=>{
        
    }, [filter])
    
    const chartState = {
        
            labels: prepData[1],
            datasets: [{
                
                data:  prepData[0],
                backgroundColor:barColors,
                hoverBackgroundColor:'#99b6bf',
                barPercentage: 1,
            }]
        
        
    }

    const AccessFilter = ()=>{
        if(access_level === '0'||access_level === '2'){
            return(
                <>
                <div style={{float:'right',fontSize:'15px', fontStyle:'italic'}}>
                    <input type="radio" value="1" name="accessLevelFilter" readOnly={true} checked={filter === 1? true:false} onClick={()=>{setFilter(1)}}/> L1<span style={{marginRight:'12px'}}></span>
                    <input type="radio" value="2" name="accessLevelFilter" readOnly={true} checked={filter === 2? true:false} onClick={()=>{setFilter(2)}}/> L2<span style={{marginRight:'12px'}}></span>
                    <input type="radio" value="0" name="accessLevelFilter" readOnly={true} checked={filter === 0? true:false} onClick={()=>{setFilter(0)}}/> All
                </div>
                </>
            )
        }
        else{
            return(<></>)
        }
    }
    

    return(
        <div  className={classes.chartHolder}>
            <span className={classes.statReportTitle}>Side-By-Side [Team]</span>
            <AccessFilter/>
            <Bar
                data={chartState}
                height={72}
                width={200}
                options= {{
                    responsive:true,
                    maintainAspectRatio: true,
                    plugins:{
                        legend:{
                            display: false,
                        }     
                    },
                    
                }}
                
                
            /> 
        </div>
    )
}


export const MonthOverMonthCategoryChart = () =>{
    const classes = useStyles();
    const state = useSelector(state => state)
    var cat_index = 0
    const category = state.ChartPull.category
    const categoryTerm = state.ChartPull.categoryTerm
    if(categoryTerm === 'callsTaken'){cat_index = 0}
    if(categoryTerm === 'missedCalls'){cat_index = 1}
    if(categoryTerm === 'sn_closedCase'){cat_index = 2}
    if(categoryTerm === 'sn_openTotal'){cat_index = 3}
    if(categoryTerm === 'sn_openTask'){cat_index = 4}
    if(categoryTerm === 'sn_openCase'){cat_index = 5}
    if(categoryTerm === 'C2C%'){cat_index = 6}
    if(categoryTerm === 'heroPoints'){cat_index = 7}
    const selectedTech = state.ChartPull.selectedTech
    const mom_tech_data = state.ChartPull.mom_tech_data[cat_index]
    const mom_team_data = state.ChartPull.mom_team_data[cat_index]
    console.log(mom_team_data)
    console.log(state)
    const month = []
    const value = []
    for(const i in mom_team_data){
        month.push(mom_team_data[i][0])
        value.push(mom_team_data[i][1])
    }
    const chartState = {
        labels: mom_tech_data[1][0],
            datasets: [
                {
                label: selectedTech,
                    data: mom_tech_data[1][1],
                    fill: false,
                    backgroundColor:accentMain,
                    borderColor:accentMain,
                    
                },
                {
                label: 'Team Median',
                    data: value,
                    fill: false,
                    backgroundColor:tint,
                    borderColor: tint,
                    borderDash: [20,8]
                }
            ]

    }
    return(
        <div  className={classes.chartHolder}>
            <span className={classes.statReportTitle}>Month-Over-Month [Median]</span>
            <Line
                data={chartState}
                height={72}
                width={200}
                options= {{
                    responsive:true,
                    maintainAspectRatio: true,
                    plugins:{
                        legend:{
                            display: true,
                        }     
                    },
                    
                }}
                
                
            /> 
        </div>

    )
    
}





///TOOLS///
const accessDataFilter = (data,accesslevel,filter,tech) =>{

	var new_data = []
	if(accesslevel === '1'){
		for(const i in data){
			if((data[i].accessLevel).toString() === accesslevel){
				new_data.push(data[i])
			};
		};
	};
	if(accesslevel === '0' || accesslevel ==='2'){
        if(filter ===1 || filter ===2){
            for(const i in data){
                if((data[i].accessLevel) === filter){
                    new_data.push(data[i])  
                };
                if((data[i].tech) === tech && data[i].accessLevel !== filter){
                    new_data.push(data[i]) 
                }
            };
        };
        if(filter ===0){
            for(const i in data){
                if(data[i].accessLevel === 1 || data[i].accessLevel === 2 ){
                    new_data.push(data[i])                }
            } 
        }
    };
	return new_data
};


const graphPrep =(data_sorted,tech,categoryTerm)=>{
    
	var _cat = categoryTerm
	const grouped_dataArray = []
	const grouped_nameArray = []
	for (const i in data_sorted){
		grouped_dataArray.push(data_sorted[i][_cat])
	}
	for (const i in data_sorted){
		grouped_nameArray.push(data_sorted[i].tech)
	}
	var dataArray = [grouped_dataArray,grouped_nameArray]
	return dataArray
};

const dataCheck = (data) =>{
    console.log(data)
    if(data[0]){
        return data
    }else{
        return [[0,0],[0,0]]
    }
}

