// import {DefaultState} from './RootReducer';

export const DefaultState = {
    loading: false,
    errorMsg:'',
    userEmail:'',
    userName:'',
    userAccessLevel:'',
    data: [],
    msg:'',
    sesKey:'',

};

const UserAuthReducer = (state = DefaultState, action) => {
    switch (action.type) {
        case "AUTHORIZING_SESSION":
  
            return {
                ...state,
                loading: true,
                msg:'authorizing session',
                sesKey: action.sesKey
            };
        case "SESSION_AUTHORIZED":
            return {
                ...state,
                loading: false,
                msg: action.payload[0],
                userEmail:action.payload[1],
                userName:action.payload[2],
                userAccessLevel:action.payload[3][0],
                sesKey: action.sesKey



            };
        case "SESSION_FAIL":
            return {DefaultState,
                sesKey: action.sesKey
            };

        case "AUTHORIZE_LOADING":
            return {
                ...state,
                loading: true,
                errorMsg:''
            };
        case "AUTHORIZE_FAIL":
            return {
                ...state,
                loading: false,
                errorMsg: 'Unable to login'
            };
        case "AUTHORIZE_SUCCESS_LOGIN":
            return {
                ...state,
                loading: false,
                data: action.payload[0],
                sesKey: action.payload[1],
                errorMsg:''
            };
        case "AUTHORIZE_SUCCESS_REGISTER":
            return {
                ...state,
                loading: false,
                data: action.payload,
                errorMsg:''
            };
        case "LOG_OUT":
            return {DefaultState,
                    };

        default:
            return state
    }
};

export default UserAuthReducer