import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import Grid from '@material-ui/core/Grid';
import { useDispatch, useSelector } from 'react-redux';
// import {UpdateSession, UserAuth} from '../actions/EmployeeActions';
import _, { set } from 'lodash';
import useStyles from '../styles';
import techSupportLogo from '../images/techSupportLogo.png' 
import {LoginAuth, SessionAuth} from '../actions/EmployeeActions';



function SignIn() {
    console.log("routed to login screen")
    
    const classes = useStyles();
    const [email,setEmail] = useState();
    const [password,setPassword] = useState();
    const [isChecked, setIsChecked] = useState(false);
    const [signInButton, setSignInButton] = useState("Login");
    const [errorShown, setErrorShown] = useState(0);
    const [performAuth, setPerformAuth] = useState(false);
    const [registerShown, setRegisterShown] = useState(false);
    const [message, setMessage] = useState();
    const [pageLoaded, setPageLoaded] = useState(false);
    

    const dispatch = useDispatch();
    // const FullState = useSelector(state => state);
    // console.log(FullState)
    const loginInfo = useSelector(state => state.UserAuth);
    const sessionKey = loginInfo.sesKey


    // console.log(loginInfo)
    
    useEffect(() => {
        setMessage(loginInfo.data)
        if(errorShown != 0){setErrorShown(0)}
        if(!performAuth) return
        if(performAuth){
            Auth()
        }
        
                
    }, [loginInfo,performAuth,setIsChecked,setMessage]);
    
    
    const userName = 'Steven'
    const storedEmail = ''
    if(localStorage.getItem('cached_email')){
        const storedEmail = localStorage.getItem('cached_email');
        if(email === undefined){
            setEmail(storedEmail)
            setIsChecked(true)
        } 
        if(isChecked === false){
            localStorage.removeItem('cached_email');
        }     
    }
    


    if(sessionKey){
        console.log(sessionKey);
        localStorage.setItem('session_key', sessionKey);
        window.location.assign('/reports');
    }

    if(!sessionKey){ 
        if(message ==='Incorrect Password'){
            if(errorShown != 2){
                setErrorShown(2);
            }
        }
        if(message ==='User does not exist'){
            if(errorShown != 2){
                setErrorShown(2);
            }
        }
        
        if(message ==='Please register'){
            if(errorShown != 4){
                setErrorShown(4);
                setSignInButton('Register');
                setRegisterShown(true);
            }
        }
        
    }
   
    if(message ==='Successful Registration'){
 
        if(errorShown != 7){

            setErrorShown(7);
            setSignInButton('Login');
            setRegisterShown(false);
        }
    }

    const Auth = () => {

        dispatch(LoginAuth(email,password,signInButton))
        setPerformAuth(false)
        return
    }

    const Login = () => {
        setErrorShown(0)  
		if(isChecked === true){
			localStorage.setItem('cached_email',email)
		}
		if(isChecked === false){
			if(localStorage.getItem('cached_email')){
				localStorage.removeItem('cached_email');
			}
		}
        if(_.isEmpty(email)){
            setErrorShown(1);
        }
        else if(_.isEmpty(password)){
            setErrorShown(1);
        }
        else{
            setPerformAuth(true)        
        }
    }

    return (
        <div className={classes.root}>
            <Grid container spacing={0} direction="column" justify='center' alignItems="center" style={{ minHeight:'100vh'}} >
                <Grid item xs={8} s={6} lg={3} container className={classes.loginScreen} justify='center' alignItems="center" style={{ minHeight:'50vh'}}>

                {/* <div style={{width:'400px',height:'200px', backgroundColor:'#fff'}}>{userInfo.data}</div> */}

                <div id='formGrid' className={classes.loginForm}>
                    <img src={techSupportLogo} className={classes.logo}/>
                    <input type="text" placeholder="Email" defaultValue={email} className={classes.inputs} onChange={(e)=>{setEmail(e.target.value);setErrorShown(0);setMessage('')}}/>
                    <input type="password" placeholder="Password" defaultValue='' className={classes.inputs} onChange={(e)=>{setPassword(e.target.value);setErrorShown(0);setMessage('')}}/>
                    <input type="password" placeholder="Confirm Password" defaultValue='' className={classes.inputs} style={{ display: registerShown === true? 'flex':'none' }} onChange={(e)=>{setPassword(e.target.value);setMessage('')}}/>
                    <label id='remLabel'><input type="checkbox" checked={isChecked} onChange={(e)=>{setIsChecked(e.target.checked);setErrorShown(0)}}/>Remember me on this computer</label>
                    
                    <button className={classes.paperSignInButton} onClick={Login} >{signInButton}</button>
                    <span id='Error_Empty' className={classes.alertFailure} style={{ display: errorShown === 1? 'flex':'none' }}>Please fill in both fields</span>
                    <span id='Error_EmailPassword' className={classes.alertFailure} style={{ display: errorShown === 2? 'flex':'none' }}>Please enter correct email and password</span>
                    <span id='Error_SessionExpired' className={classes.alertFailure} style={{ display: errorShown === 3? 'flex':'none' }}>Session has expired, login again</span>
                    <span id='Error_Register' className={classes.alertSuccess} style={{ display: errorShown === 4? 'flex':'none' }}>Welcome, please register to use the platform</span>
                    <span id='Error_RegisterExist' className={classes.alertFailure} style={{ display: errorShown === 5? 'flex':'none' }}>User already registered</span>
                    <span id='Error_PWlength' className={classes.alertFailure} style={{ display: errorShown === 6? 'flex':'none' }}>Password must be at least 4 characters</span>
                    <span id='Success_Register' className={classes.alertSuccess} style={{ display: errorShown === 7? 'flex':'none' }}>Successfully registered, Login</span>
                    <span id='Error_PWconfirm' className={classes.alertFailure} style={{ display: errorShown === 8? 'flex':'none' }}>Password mis-match</span>
                    <span id='Success_Login' className={classes.alertSuccess} style={{ display: errorShown === 9? 'flex':'none' }}>Welcome back {userName}!</span>
                 
                    <p id='ForgotPassword_' style={{fontSize:'14px'}}>Forgot your password? Email scasimir@focusbrands.com</p>
                </div>
                

                <div id='loading'></div>
                   
                   
                 </Grid>
            </Grid>
                
            
        </div>
    )
}

export default SignIn;