import axios from 'axios';
import {useDispatch, useSelector} from 'react-redux'


export const CancelLoading = ()=> async dispatch=>{
    dispatch({
        type:'CANCEL_LOAD'
    })
}

export const DataGrab = (tech_name, month) => async dispatch => {

    try{
        dispatch({
            type: 'LOADING_DATA'      
        })
        const res =  await axios.get("https://fbmonthpullhttp.azurewebsites.net/api/fb-monthPull-Http?month="+month+"&tech="+tech_name)
        dispatch({
            type: 'DATA_RETRIEVED',
            payload: res.data,
        })
       
    } catch (e){   
        console.log(e)
        dispatch({
            type: 'DATA_FAIL',
           
        })
    }
};

