export const MonthOverMonth_DataGrab = (tech_email) => async dispatch => {
    try{
        dispatch({
            type: 'MOM_LOADING_DATA'      
        })
        const res =  await axios.get("http://localhost:7071/api/fb-MonthOverMonth-http?tech="+tech_email)
        dispatch({
            type: 'MOM_DATA_RETRIEVED',
            payload: res.data,
        })
    } catch (e){   
        console.log(e)
        dispatch({
            type: 'MOM_DATA_FAIL',
           
        })
    }
};

export const SetCategory = (category)=> dispatch =>{
 
    if(category === 'Calls Taken'){
        dispatch({
            type: category
        })
    }
    if(category === 'Missed Calls'){
        dispatch({
            type: category
        })
    }
    if(category === 'Cases Opened'){
        dispatch({
            type: category
        })
    }
    if(category === 'Task Opened'){
        dispatch({
            type: category
        })
    }
    if(category === 'Total Opened'){
        dispatch({
            type: category
        })
    }
    if(category === 'Closed Cases'){
        dispatch({
            type: category
        })
    }
    if(category === 'Hero Points'){
        dispatch({
            type: category
        })
    }
};






