import React, { useState, useEffect } from 'react'
import { isTemplateSpan } from 'typescript';
import useStyles from '../styles';
import axios from 'axios';
import { useDispatch, useSelector} from 'react-redux';
import _ from 'lodash';
import {GetEmployeeList, UserAuth} from '../actions/EmployeeActions';
import {SetSelectedTech} from '../actions/QaFormActions'
import {DataGrab} from '../actions/ReportingActions';
import PersonIcon from '@material-ui/icons/Person';
// import {RootReducer, DefaultState} from '../reducers/RootReducer';





const QaTechSelectComponent = (props) => {
    const classes = useStyles();
    const [monthList] = useState([
        {label:"Month...",value:'0'},
        {label:"Jan",value:'Jan'},
        {label:"Feb",value:'Feb'},
        {label:"Mar",value:'Mar'},
        {label:"Apr",value:'Apr'},
        {label:"May",value:'May'},
        {label:"Jun",value:'Jun'},
        {label:"Jul",value:'Jul'},
        {label:"Aug",value:'Aug'},
        {label:"Sep",value:'Sep'},
        {label:"Oct",value:'Oct'},
        {label:"Nov",value:'Nov'},
        {label:"Dec",value:'Dec'},
    ]);
    const [selectedTech, setSelectedTech] = useState('');
    const [selectedMonth, setSelectedMonth] = useState();

    const dispatch = useDispatch();
    const techList = useSelector(state => state.EmployeeList);
    const loading = useSelector(state => state)
    const userName = useSelector(state => state.UserAuth.userName)
    const userAccessLevel = useSelector(state => state.UserAuth.userAccessLevel)
 
    useEffect(() => {
            FetchData()
            if(selectedTech===''){setSelectedTech(userName)}
 
    }, [userName]);

    
    const FetchData = () => {
        if(techList.employeeList.length < 1){
            dispatch(GetEmployeeList())
        }  
    }

    const LoadList = () => {
        
        
        if (!_.isEmpty(techList)){      
            if(userAccessLevel==='0'){
                return(
                
                    <select id='tech_select' className={classes.paperSelect} onChange={(e)=>{setSelectedTech(e.target.value);dispatch(SetSelectedTech(e.target.value))}}> 
                        <option key={0} value={'Tech Select'}>Tech Select...</option>  
                        {techList.employeeList.map((tech,index)=> {
                            const label = tech;
                            const value = index+1;
                            return (
                            <option key={value} value={label}>
                                {label}
                            </option>
                            );
                        })};
                    </select>     
                )
            } 
            if(userAccessLevel==='1'||userAccessLevel==='2'){
                return(
                    <select id='tech_select' className={classes.paperSelect} > 
                        <option key={0} defaultValue={userName}>{userName}</option>  
                    </select>  
                )
            }
            else{
                return(
                    <select id='tech_select' className={classes.paperSelect}> 
                        <option key={0} defaultValue={'No User Found'}>{'Loading...'}</option>    
                    </select>  

                )
            }
        }

        if (techList.loading){
            return(
                <select id='tech_select' className={classes.paperSelect}>     
                    <option key={0} value={'loading'}> Loading...</option>
                </select>
            )
        }
        if (techList.errorMsg !== ""){
            return(
                <select id='tech_select' className={classes.paperSelect}>     
                    <option key={0} value={'error'}> [Error] No Data...</option>
                </select>
            )
        }
    };
    
    return (
        <>
        
        <div>
            <li style={{listStyleType:'none'}} >
                <div>
                    {LoadList()}
                
                    {/* <button id='qa_submit_run' className={classes.paperFlatButton} style={{}} onClick={()=>{console.log('submit qa')}}>SUBMIT</button> */}
                    
                </div>  

            </li>
        </div>
        </>
        )
    }

export default QaTechSelectComponent;