
export const Default_QaState = {
    q1:4,
    q2:6,
    q3:6,
    q4:4,
    q5:4,
    q6:4,
    q7:4,
    q8:6,
    q9:6,
    q10:5,
    q11:5,
    q12:5,
    q13:6,
    q14:5,
    q15:4,
    q16:5,
    q17:5,
    q18:5,
    q19:6,
    q20:5,
    qa_submitting:false,
    qa_techSelected:'',
    qa_caseNumber:'',
    qa_evalPeriod:0,
    qa_caseCloseDate:0,
    qa_storeId:'',
    qa_contactPerson:'',
    qa_caseOpenDate:0,
    qa_callDate:0,
    qa_comment:''

    


}



const QaReducer = (state = Default_QaState, action)=>{
    
    switch (action.type) {
        case "UPDATE_FORM":
            switch(action.field){
                case 'q1':return{...state,q1:action.value}
                case 'q2':return{...state,q2:action.value}
                case 'q3':return{...state,q3:action.value}
                case 'q4':return{...state,q4:action.value}
                case 'q5':return{...state,q5:action.value}
                case 'q6':return{...state,q6:action.value}
                case 'q7':return{...state,q7:action.value}
                case 'q8':return{...state,q8:action.value}
                case 'q9':return{...state,q9:action.value}
                case 'q10':return{...state,q10:action.value}
                case 'q11':return{...state,q11:action.value}
                case 'q12':return{...state,q12:action.value}
                case 'q13':return{...state,q13:action.value}
                case 'q14':return{...state,q14:action.value}
                case 'q15':return{...state,q15:action.value}
                case 'q16':return{...state,q16:action.value}
                case 'q17':return{...state,q17:action.value}
                case 'q18':return{...state,q18:action.value}
                case 'q19':return{...state,q19:action.value}
                case 'q20':return{...state,q20:action.value}
                case 'qa_comment':return{...state,qa_comment:action.value}
                default: return state

            }
        case "QA_SET_SELECTED_TECH":
            return{
                ...state,qa_techSelected:action.value
            }
        case "QA_SET_SUBMIT":
            return{
                ...state,qa_submitting:action.value
            }
        case "QA_SUBMITTED":
            alert('Successfully Submitted QA Data')
            return{
                ...state,qa_submitting:false,
            }
        case "QA_DATA_FAIL":
            alert('Qa Data Failed to Submit, please retry or refresh page')
            return{
                ...state,qa_submitting:false
            }
        default:
            return state
    }
}
export default QaReducer