import React, {useState, useEffect} from 'react'
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { useDispatch, useSelector } from 'react-redux';
import _ from 'lodash';
import useStyles from '../styles';




const ScoreCardComp =(props)=>{
    const classes = useStyles();
    const techCurrentMonthQa = useSelector(state => state.ChartPull.chartData[2])

    if(techCurrentMonthQa !== undefined){
        if(techCurrentMonthQa[0]){
            const techName =techCurrentMonthQa[0].PartitionKey
            return(
                
                <Grid item container className={classes.qaScoreTable}>
                    <Grid item><h4>{techName}'s Quality Assurance</h4></Grid>
                    {techCurrentMonthQa.map((qaEntry,index) =>{
                        const key = index
                        const entryDate = qaEntry.RowKey.slice(0, qaEntry.RowKey.length -5)
                        // str = str.slice(0, str.length - 1);

                        const score = parseInt(qaEntry.Score)
                        const callDate = qaEntry.call_date
                        const closedDate = qaEntry.case_closed
                        const caseNum = qaEntry.case_num
                        const caseOpened = qaEntry.case_opened
                        const comment = qaEntry.comment
                        const store = qaEntry.store
                        return(
                            <Grid item container  className={classes.qaScoreCard} key={key}>
                                <Grid item container xs={6}><div className={classes.qaScoreCard_score}>{score}</div><div className={classes.qaScoreCard_caseNum} >{caseNum}</div></Grid>
                                <Grid item container xs={6} justify='flex-end'><div className={classes.qaScoreCard_entryDate} ><div style={{fontSize:'12px'}}>submitted:</div>{entryDate}</div></Grid>
                                <Grid item container xs={12}><div className={classes.qaScoreCard_comment} >{comment}</div></Grid>
                            </Grid>
                        )
                        })
                    }
                 
                </Grid> 
            )
        }else{return(<Grid item container className={classes.qaScoreCard} style={{width:'97%'}}><Grid item container><div className={classes.qaScoreCard_entryDate} >No QA Data</div></Grid></Grid>)}
    }else{return(<Grid item container className={classes.qaScoreCard} style={{width:'97%'}}><Grid item container><div className={classes.qaScoreCard_entryDate} >No QA Data</div></Grid></Grid>)}
}

export default ScoreCardComp