import React, {useState, useEffect} from 'react'
import QaTechSelectComponent from '../keyComps/QA_techSelectComp';
import Grid from '@material-ui/core/Grid';
import Input from '@material-ui/core/Input';
import {DatePicker} from '@material-ui/pickers';
import { useDispatch, useSelector} from 'react-redux';
import {UpdateQaForm_q,UpdateQaForm_d, SubmitQaData, SetSubmit} from '../actions/QaFormActions';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import WorkIcon from '@material-ui/icons/Work';
import PersonIcon from '@material-ui/icons/Person';
import StoreIcon from '@material-ui/icons/Store';
import {ScoreSum, DateStringBuildFull,DateStringBuildShort} from '../helperFunctions/helperQaForm';

import useStyles from '../styles'


export const ScoreCommentSubmit = ()=>{
    const classes = useStyles();
    const dispatch = useDispatch();
    const state = useSelector(state => state.Qa_formdata)
    const submit = state.qa_submitting
    const comment = state.qa_comment


    // useEffect(()=>{


    // },[state])
  
    
    const score = ScoreSum(state)

    return(
        <Grid item container xs={6} direction={'column'} className={classes.qaForm_Case}>
            
            <div><textarea className={classes.qaForm_comment} onChange={(e)=>{dispatch(UpdateQaForm_d('qa_comment',e.target.value))}}></textarea></div>
            <div>
                <div className={classes.qaForm_Score} >Score: {score}</div>
                <button className={submit? classes.qaPaperFlatButtonSubmitting:classes.qaPaperFlatButton} onClick={()=>{dispatch(SetSubmit(true))}}>Submit</button>
            </div>
            

        </Grid>
    )
}


export const QaFieldComp = ()=>{
    const classes = useStyles();
    const dispatch = useDispatch();
   
    


    return (
        
        <Grid item container xs={6} direction={'row'} className={classes.qaForm_Case}>
            <ul>
                <h3 className={classes.qaForm_Header}>Customer Experience</h3>
                <div style={{marginTop:'5px'}}>
                {QA_CustomerExperience.map((data,index)=>{
                    const key = index+1
                    const number = data.number
                    const question= data.question
                    const value = data.values
                    const optValue = data.optValues 
                    return(
                        <Grid key={key} style={{padding:'1px'}}>
                            <label className={classes.qaForm_Label}>{number}.</label><label className={classes.qaForm_Label}>{question}</label>
                            <select className={classes.qaForm_paperSelect} onChange={(e)=>{dispatch(UpdateQaForm_q(key,e.target.value))}}>
                                <option value={value[0]}>{optValue[0]}</option>
                                <option value={value[1]}>{optValue[1]}</option>
                                <option value={value[2]}>{optValue[2]}</option>
                                <option value={value[3]}>{optValue[3]}</option>
                            </select>
                        </Grid>
                       
                    )

                })}
                </div>
            </ul>
            <ul>
                <h3 className={classes.qaForm_Header}>Customer Experience</h3>
                <div style={{marginTop:'5px'}}>
                {QA_TroubleShooting.map((data,index)=>{
                    const key = index+8
                    const number = data.number
                    const question= data.question
                    const value = data.values
                    const optValue = data.optValues 
                    return(
                        <Grid key={key} style={{padding:'1px'}}>
                            <label className={classes.qaForm_Label}>{number}.</label><label className={classes.qaForm_Label}>{question}</label>
                            <select className={classes.qaForm_paperSelect} onChange={(e)=>{dispatch(UpdateQaForm_q(key,e.target.value))}}>
                                <option value={value[0]}>{optValue[0]}</option>
                                <option value={value[1]}>{optValue[1]}</option>
                                <option value={value[2]}>{optValue[2]}</option>
                                <option value={value[3]}>{optValue[3]}</option>
                            </select>
                        </Grid>
                       
                    )

                })}
                </div>
            </ul>
            <ul>
                <h3 className={classes.qaForm_Header}>Customer Experience</h3>
                <div style={{marginTop:'5px'}}>
                {QA_CaseManagement.map((data,index)=>{
                    const key = index+13
                    const number = data.number
                    const question= data.question
                    const value = data.values
                    const optValue = data.optValues 
                    return(
                        <Grid key={key} style={{padding:'1px'}}>
                        <label className={classes.qaForm_Label}>{number}.</label><label className={classes.qaForm_Label}>{question}</label>
                        <select className={classes.qaForm_paperSelect} onChange={(e)=>{dispatch(UpdateQaForm_q(key,e.target.value))}}>
                            <option value={value[0]}>{optValue[0]}</option>
                            <option value={value[1]}>{optValue[1]}</option>
                            <option value={value[2]}>{optValue[2]}</option>
                            <option value={value[3]}>{optValue[3]}</option>
                        </select>
                    </Grid>
                       
                    )

                })}
                </div>
            </ul>

        </Grid>
    ) 
}


export const QaInfo = ()=>{
    const state = useSelector(state => state.Qa_formdata)
    const dispatch = useDispatch();
    const date = new Date()
    const submittingStatus = state.qa_submitting
    const techSelected = state.qa_techSelected
    const [storeId, setStoreId] = useState();
    const [caseNum, setCaseNum] = useState();
    const [contactPerson, setContactPerson] = useState();
    const [evalPeriod, setEvalPeriod] = useState(date);
    const [caseOpenDate, setCaseOpenDate] = useState(date);
    const [caseCloseDate, setCaseCloseDate] = useState(date);
    const [callDate, setCallDate] = useState(date);
    const score = ScoreSum(state)
    
    const comment = state.qa_comment

    const classes = useStyles();
    
    useEffect(()=>{
        if(submittingStatus){
            if(techSelected){
                
                
                dispatch(SubmitQaData(
                    techSelected,
                    storeId,
                    caseNum,
                    contactPerson,
                    DateStringBuildFull(evalPeriod),
                    DateStringBuildShort(caseOpenDate),
                    DateStringBuildShort(caseCloseDate),
                    DateStringBuildShort(callDate),
                    score,
                    comment

                    ))
            
            }else{
                alert('Please Complete All Fields!')
            }

            }
            

    },[submittingStatus])


    return(
        <Grid item container xs={6} direction={'row'} className={classes.qaForm_Case}>
            <Grid item container xs={6} lg={3} style={{padding:'4px'}}><div className={classes.qaForm_InfoLabel}>Tech Selected </div></Grid><Grid item xs={3}><PersonIcon style={{float:'left',marginRight:'10px'}}/><QaTechSelectComponent/></Grid>
            <Grid item container xs={6} lg={3} style={{padding:'4px'}}><div className={classes.qaForm_InfoLabel}>Restaurant ID: </div></Grid><Grid item xs={3}><StoreIcon style={{marginRight:'10px'}}/><Input placeholder='mcal123' className={classes.qaForm_paperSelect} style={{width:'180px',fontSize:'16px',padding:'0px',paddingLeft:'5px',height:'28px',}} onChange={(e)=>{setStoreId(e.target.value)}}></Input></Grid>
            <Grid item container xs={6} lg={3} style={{padding:'4px'}}><div className={classes.qaForm_InfoLabel}>Case #:: </div></Grid><Grid item xs={3}><WorkIcon style={{marginRight:'10px'}}/><Input placeholder='CS00#' className={classes.qaForm_paperSelect} style={{width:'180px',fontSize:'16px',padding:'0px',paddingLeft:'5px',height:'28px'}} onChange={(e)=>{setCaseNum(e.target.value)}}></Input></Grid>
            <Grid item container xs={6} lg={3} style={{padding:'4px'}}><div className={classes.qaForm_InfoLabel}>Contact Person: </div></Grid><Grid item xs={3}><PersonIcon style={{marginRight:'10px'}}/><Input  placeholder='Bob the builder' className={classes.qaForm_paperSelect} style={{width:'180px',fontSize:'16px',padding:'0px',paddingLeft:'5px',height:'28px'}}onChange={(e)=>{setContactPerson(e.target.value)}}></Input></Grid>
            <Grid item container xs={6} lg={3} style={{padding:'4px'}}><div className={classes.qaForm_InfoLabel}>Evaluation Period: </div></Grid>
                <Grid item xs={6} lg={3}><CalendarTodayIcon style={{marginRight:'10px'}}/>
                    <DatePicker format='MMM' value={evalPeriod} onChange={setEvalPeriod}className={classes.paperSelect} style={{width:'180px',fontSize:'16px',padding:'0px',paddingLeft:'5px',height:'28px'}}></DatePicker>
                </Grid>
            <Grid item container xs={6} lg={3} style={{padding:'4px'}}><div className={classes.qaForm_InfoLabel}>Case Opened Date: </div></Grid><Grid item xs={3}><CalendarTodayIcon style={{marginRight:'10px'}}/>
                    <DatePicker format='MMM d, yyyy' value={caseOpenDate} onChange={setCaseOpenDate}className={classes.paperSelect} style={{width:'180px',fontSize:'16px',padding:'0px',paddingLeft:'5px',height:'28px'}}></DatePicker>
                </Grid>
            <Grid item containerxs={6} lg={3} style={{padding:'4px'}}><div className={classes.qaForm_InfoLabel}>Case Closed Date: </div></Grid><Grid item xs={3}><CalendarTodayIcon style={{marginRight:'10px'}}/>
                    <DatePicker  format='MMM d, yyyy' value={caseCloseDate} onChange={setCaseCloseDate}className={classes.paperSelect} style={{width:'180px',fontSize:'16px',padding:'0px',paddingLeft:'5px',height:'28px'}}></DatePicker>
                </Grid>
            <Grid item container xs={6} lg={3} style={{padding:'4px'}}><div className={classes.qaForm_InfoLabel}>Date of Call: </div></Grid><Grid item xs={3}><CalendarTodayIcon style={{marginRight:'10px'}}/>
                    <DatePicker  format='MMM d, yyyy' value={callDate} onChange={setCallDate}className={classes.paperSelect} style={{width:'180px',fontSize:'16px',padding:'0px',paddingLeft:'5px',height:'28px'}}></DatePicker>
                </Grid>
        </Grid>
        
                
        
       
    )
}


