import React, {useState, useEffect} from 'react'
import Grid from '@material-ui/core/Grid';
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress'
import { useDispatch, useSelector} from 'react-redux';
import _ from 'lodash';
import useStyles from '../styles';
import TechSelectComponent from '../keyComps/techSelectComp';
import ScoreCardComp from '../keyComps/scoreCardComp'
import TechStatsButtonComp from '../keyComps/techStatsButtonComp';
import {CategoryChart, MonthOverMonthCategoryChart} from '../keyComps/categoryTeamChart';
import {DataGrab, Reload_DataGrab, MonthOverMonth_DataGrab,CancelLoading} from '../actions/ReportingActions';
import { SessionAuth } from '../actions/EmployeeActions';



function Reports() {
    const dispatch = useDispatch();
    const classes = useStyles();
    const store = useSelector(state => state);
    const [reportData, setReportData] = useState();
    const loading = store.ChartPull.loading;
    const category = store.ChartPull.category;
    const month = store.ChartPull.month;
    const reload = store.ChartPull.reload;
    const SesKey = store.UserAuth.sesKey;
    const selectedTech = store.ChartPull.selectedTech;
    const time = new Date();
    const [email, setEmail] = useState()
    
    time.setSeconds(time.getSeconds() +10);
    
    
    useEffect(()=>{
        if(store.ChartPull.chartData[1]){
            setReportData(store.ChartPull)
            const selectedTechEmail = store.ChartPull.selectedTechEmail;
            if(email !== selectedTechEmail){
                setEmail(selectedTechEmail)
                dispatch(MonthOverMonth_DataGrab(selectedTechEmail))
            }
        }
        const interval = setInterval(()=>{
            if(reload){
                console.log('it reloaded')
                dispatch(SessionAuth(SesKey))
                dispatch(Reload_DataGrab(store.ChartPull.selectedTech,store.ChartPull.month))
            }
        }, 120000);
        return () => clearInterval(interval);

    },[store])
    
    return (
        <div className={classes.root}>
            <div style={{position:'fixed',top:'33px',left:'100px'}}>
                <TechSelectComponent />
            </div>           

            <div className={classes.belowNavBar}>
                {!loading? loading :
                    <Backdrop className={classes.backdrop} open >
                        <CircularProgress className={classes.backdropCircle} size={'100px'}></CircularProgress>
                        <div style={{position:'absolute'}}>LOADING...</div>
                    </Backdrop>
                }
            {/* Main bottom section*/}
                <Grid container direction={'row'} justify={'center'} alignItems={'center'} className={classes.belowNavBarBody}>

                {/* Left Section section*/}
                    <Grid item container  xs={2} lg={2} className={classes.reportLeftGrid} direction={'column'} >
                        <TechStatsButtonComp/>

                    </Grid>

                {/* Right section*/}
                    <Grid item container xs={10} lg={10} className={classes.reportRightGrid}>

                        <Grid item container direction={'column'} xs={8}>
                            
                            <Grid item container direction={'column'} xs={12} >
                                <Grid item >
                                    <h4 className={classes.title} style={{margin:'5px',height:'20px'}} >{reportData ? month+' '+category+' data for '+ selectedTech  : 'Select Tech and Run for Reports...'}
                                        
                                    </h4>
                                </Grid>
                                <Grid item >
                                    <CategoryChart />
                                    <MonthOverMonthCategoryChart/>
                                </Grid>
                            </Grid>

                            
                                
                                
                                {/* <Grid item container><CategoryChart/></Grid> */}
                            
                        </Grid>
                        
                        

                        {/*Right section-right panel */}
                        <Grid item container xs={4} > 
                            <Grid item container direction={'column'} > 
                                <ScoreCardComp /> 
                            </Grid>
                
                        </Grid>

                    </Grid>
                    
                    
                    
                </Grid>




            </div>
        </div>
    )
}

export default Reports;

