import { useSelector} from 'react-redux';
import React, {useState, useEffect} from 'react'



function padLeadingZeros(num, size) {
    var s = num+"";
    while (s.length < size) s = "0" + s;
    return s;
}

export const ScoreSum = (state)=>{
    const sum = state.q1+state.q2+state.q3+state.q4+state.q5+
    state.q6+state.q7+state.q8+state.q9+state.q10+state.q11+
    state.q12+state.q13+state.q14+state.q15+state.q16+
    state.q17+state.q18+state.q19+state.q20
    return sum
}

export const DateStringBuildFull = (date) =>{
    
    const year = date.getFullYear()
    const month = padLeadingZeros(date.getMonth()+1,2)
    
    return ""+year+"-"+month+""
}

export const DateStringBuildShort = (date) =>{
    
    const year = date.getFullYear()
    const month = padLeadingZeros(date.getMonth()+1,2)
    const dayNum =  padLeadingZeros(date.getDate(),2)
    

    return ""+year+"-"+month+"-"+dayNum+""
}