import React, { useState, useRef, useEffect, useReducer, useContext } from 'react';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import {useSelector, useDispatch} from 'react-redux';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import Navbar from './components/Navbar';
import Forms from './pages/Forms';
import Home from './pages/Home';
import Library from './pages/Library';
import Reports from './pages/Reports';
import Team from './pages/Team';
import SignIn from './pages/Login';
import {SessionAuth, LogoutSession} from './actions/EmployeeActions';


function App() {
  const dispatch = useDispatch();
  const [cached_email, setCachedEmail] = useState();
  const userInfo = useSelector(state => state.UserAuth);
  const userEmail = userInfo.userEmail;
  const userName = userInfo.userName;
  const userAccessLevel = userInfo.userAccessLevel;
  const message = userInfo.msg;
  const loading = useSelector(state => state.ChartPull.loading)

  var SesKey = ''
  if(localStorage.getItem('session_key')){
    var SesKey = (localStorage.getItem('session_key'));
    if(userInfo.sesKey === 'SESSION_FAILED'){
      console.log(userInfo.sesKey)
      localStorage.removeItem('session_key')
      dispatch(LogoutSession())
      window.location.assign('/');
      if(cached_email){
        localStorage.setItem('cached_email',cached_email)
    }
    }
  }


  useEffect(() => {
  FetchData()


  }, [userAccessLevel]);

  const FetchData = () => {
    if(SesKey){
     
      if(!userInfo.msg){
        dispatch(SessionAuth(SesKey))
      }
    }
      
  }
  
  if(localStorage.getItem('cache_email')){
    const cached_email = localStorage.getItem('cached_email')
    setCachedEmail(cached_email)
  }
  
  if(!localStorage.getItem('cache_email')){
    if(cached_email){
      localStorage.setItem('cached_email', cached_email)
    }

  }
  
  if(SesKey !== ''){
    // console.log('session exists')
    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <Router>
            <Navbar/>
            <Switch>
              <Route path='/' exact component={SignIn} />
              <Route path='/reports' component={Reports} />
              <Route path='/forms' component={Forms} />
              <Route path='/library' component={Library} />
              {/* <Route path='/team' component={Team} /> */}
            </Switch> 
        </Router>
      </MuiPickersUtilsProvider>
     
    )
  }
  if(SesKey === ''){
    console.log('session does not exists')
    if(window.location.pathname !== '/'){
      window.location.assign('/');
    }
    
    
    return(
      <Router>
           
            <Switch>
              <Route path='/' exact component={SignIn} />  
            </Switch> 
        </Router> 
    )
  }
}
export default App;
