// import {DefaultState} from './RootReducer';

export const DefaultState = {
    loading: false,
    employeeList: [],
    errorMsg:''
};

const EmployeeListReducer = (state = DefaultState, action) => {

    switch (action.type) {
            
        case "EMPLOYEE_LIST_LOADING":
            return {
                ...state,
                loading: true,
                errorMsg:''
            };
        case "EMPLOYEE_LIST_FAIL":
            return {
                ...state,
                loading: false,
                errorMsg: 'unable to get employees'
            };
        case "EMPLOYEE_LIST_SUCCESS":
            
            return {
                ...state,
                loading: false,
                employeeList: action.payload,
                errorMsg:''
            };
        default:
            return state
    }
};

export default EmployeeListReducer