import axios from 'axios';
import {useDispatch, useSelector} from 'react-redux'

export const UpdateQaForm_q = (field,value)=> async dispatch=>{
  
    dispatch({
        type:'UPDATE_FORM',
        field:'q'+field,
        value:parseInt(value)}
    )
}

export const UpdateQaForm_d = (field,value)=> async dispatch=>{
    
  
    dispatch({
        type:'UPDATE_FORM',
        field: field,
        value:value}
    )
}

export const SetSelectedTech = (selectedTech) => async dispatch=>{
    
    dispatch({
        type:'QA_SET_SELECTED_TECH',
        value:selectedTech
    })
}

export const SetSubmit = (submit) => async dispatch=>{

    dispatch({
        type:'QA_SET_SUBMIT',
        value:submit
    })
}


export const SubmitQaData = (tech_name,store,case_num,contact,period,caseOpendate,caseClosedate,callDate,score,comment) => async dispatch => {

    try{
        
        const res =  await axios.post("https://qaimporthttp.azurewebsites.net/api/fb-qa-http-trigger?tech_name="+tech_name+"&store="+store+"&case_num="+case_num+"&contact="+contact+"&period="+period+"&caseOpendate="+caseOpendate+"&caseClosedate="+caseClosedate+"&callDate="+callDate+"&score="+score+"&comment="+comment+"  ")
        dispatch({
            type: 'QA_SUBMITTED',
            payload: res.data,

        })
       
    } catch (e){   
        console.log(e)
        dispatch({
            type: 'QA_DATA_FAIL',
           
        })
    }
};
