import React, {useState, useEffect} from 'react';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import { useDispatch, useSelector } from 'react-redux';
import _ from 'lodash';
import useStyles, {primaryBright,tint,accentMain,smoke,primaryDark,appBlack} from '../styles';
import {techStatsHelper, defaultStats, totalCalc, emptyStats} from '../helperFunctions/helperTechStats';
import {SetCategory} from '../actions/ReportingActions'




const TechStatsButtonComp = ()=>{
    const classes = useStyles();
    const dispatch = useDispatch();
    
    
    const allCurrentMonthData = useSelector(state => state.ChartPull.chartData)
    const techCurrentMonthData = useSelector(state => state.ChartPull.chartData[0])
    const teamCurrentMonthData = useSelector(state => state.ChartPull.chartData[1])
    const selectedCat = useSelector(state => state.ChartPull.category)
    


    const categoryNames = defaultStats
    const CategoryEmptyStats = emptyStats
    
    useEffect(()=>{
       
        
    
    },[selectedCat,allCurrentMonthData])
   
    
    if(techCurrentMonthData !== undefined){
        if(techCurrentMonthData){
            const categoryNamesData = techStatsHelper(techCurrentMonthData,teamCurrentMonthData)
            const team_totals = totalCalc(techCurrentMonthData,teamCurrentMonthData)
          
            const techName =techCurrentMonthData.PartitionKey
            return(
                <Grid item container className={classes.statHolder} >
                   {categoryNamesData.map((cat_name,index)=>{
                       const cat_teamCompare_message = team_totals[index][0]
                       const cat_teamCompare_percent = team_totals[index][1]
                       const cat_teamCompare_total = team_totals[index][2]
                        return( 
                            <div className={classes.statCard_button} key={cat_name[0]} onClick={(e)=>{dispatch(SetCategory(cat_name[0]))}}>
                                <Grid item container className={selectedCat !==cat_name[0]? classes.statCard :classes.statCard_Selected}  direction={'row'}>
                                    <Grid item container xs={12} lg={9}><div className={classes.statCard_category} style={selectedCat !==cat_name[0]? {borderColor:tint}:{borderColor:primaryBright}}>{cat_name[0]}</div></Grid>
                                    <Grid item container  xs={12} lg={3}><div className={classes.statCard_total}>{cat_name[1]}</div></Grid>
                                    <Grid item container  xs={12}>  <div className={classes.statCard_teamCompare}>{cat_teamCompare_percent}% of {cat_teamCompare_message} total ({cat_teamCompare_total})</div></Grid>
                                </Grid>
                            </div>
                        )
                    })}
                </Grid>  
            )
        
            // borderColor:tint,
        }else{return(<Grid item container>{CategoryEmptyStats.map((cat_name)=>{return( 
            <div className={classes.statCard_button} key={cat_name[0]} onClick={(e)=>{dispatch(SetCategory(cat_name[0]))}}>
                <Grid item container className={classes.statCard}  direction={'row'}>
                    <Grid item container xs={12} lg={9}><div className={classes.statCard_category} style={{borderColor:tint}}>{cat_name[0]}</div></Grid>
                    <Grid item container  xs={12} lg={3}><div className={classes.statCard_total}>{cat_name[1]}</div></Grid>
                    <Grid item container  xs={12}>  <div className={classes.statCard_teamCompare}>{0}% of {0} total ({0})</div></Grid>
                </Grid>
            </div>
        )})}</Grid>)}
            
    }else{return(<Grid item container>{CategoryEmptyStats.map((cat_name)=>{return( 
        <div className={classes.statCard_button} key={cat_name[0]} onClick={(e)=>{dispatch(SetCategory(cat_name[0]))}}>
            <Grid item container className={classes.statCard}  direction={'row'}>
                <Grid item container xs={12} lg={9}><div className={classes.statCard_category} style={{borderColor:tint}}>{cat_name[0]}</div></Grid>
                <Grid item container  xs={12} lg={3}><div className={classes.statCard_total}>{cat_name[1]}</div></Grid>
                <Grid item container  xs={12}>  <div className={classes.statCard_teamCompare}>{0}% of {0} total ({0})</div></Grid>
            </Grid>
        </div>
    )})}</Grid>)}
}

export default TechStatsButtonComp


// }else{return(<Grid item container>{categoryNames.map((cat_name)=>{return(<Grid item container key={cat_name[0]}><div >{cat_name[0]}</div><div>{cat_name[1]}</div></Grid>)})}</Grid>)}
         
// }else{return(<Grid item container>{categoryNames.map((cat_name) =>{return(<Grid item container key={cat_name[0]}><div >{cat_name[0]}</div><div>{cat_name[1]}</div></Grid>)})}</Grid>)}
