// import {DefaultState} from './RootReducer';


export const DefaultState = {
    loading: false,
    errorMsg:'',
    chartData:[],
    category:'Calls Taken',
    month:'',
    categoryTerm: 'callsTaken',
    categorySortedData:[],
    selectedTech:'No Tech Selected',
    selectedTechEmail:'',
    reload:false,

    mom_loading:false,
    mom_tech_data:[[0,0],[0,0],[0,0]],
    mom_team_data:[[0,0],[0,0],[0,0]]


};



const SortCategory = (all_data,category) =>{
    
    const tech = all_data[0]
    const team = all_data[1]
 
    var data = team
    const temp=[]
    for(const i in data){
        temp.push(data[i].tech)
        
    }
    if(!temp.includes(tech.tech)){
        data.push(tech)
    }

    var group_sorted = []
    if(category === 'Calls Taken'){
        
        group_sorted = data.sort(function(a,b){
            return b.callsTaken - a.callsTaken
        })
        
    }
    if(category === 'Missed Calls'){
        group_sorted = data.sort(function(a,b){
            return b.missedCalls - a.missedCalls
        })
    } 
    if(category === 'Cases Opened'){
        group_sorted = data.sort(function(a,b){	
            return b.sn_openCase - a.sn_openCase
        })
    } 
    if(category === 'Task Opened'){
        group_sorted = data.sort(function(a,b){
            return b.sn_openTask - a.sn_openTask
        })
    } 
    if(category === 'Total Opened'){
        group_sorted = data.sort(function(a,b){
            return b.sn_openTotal - a.sn_openTotal
        })
    } 
    if(category === 'Closed Cases'){
        group_sorted = data.sort(function(a,b){
            return b.sn_closedCase - a.sn_closedCase
        })
    } 
    if(category === 'Hero Points'){
        group_sorted = data.sort(function(a,b){
            return b.heroPoints - a.heroPoints
        })
    }
    
    return group_sorted
    // if(category === 'SLA'){
        
    // }  
    
};







const Chart1Reducer = (state = DefaultState, action) => {

    switch (action.type) {
        case "CANCEL_LOAD":
            return{
                ...state,
                loading:false
            }
        
        case "LOADING_DATA":
            return {
                ...state,
                loading: true,
                errorMsg:''
            };
        case "DATA_RETRIEVED":
            return {
                ...state,
                chartData: action.payload,
                categorySortedData: SortCategory(action.payload,state.category),
                selectedTech: action.payload[0].tech,
                selectedTechEmail:action.payload[0].email,
                loading: false,
                month: action.payload[0].RowKey,
                errorMsg:'',
                reload:true,
                // mom_tech_data:[],
                // mom_team_data:[]
            };
        case "DATA_FAIL":
            return {
                ...state,
                loading: false,
                errorMsg: 'Unable to get data',
                reload:false
            };
        case "RELOAD_DATA_RETRIEVED":
            return {
                ...state,
                chartData: action.payload,
                categorySortedData: SortCategory(action.payload,state.category),
                selectedTech: action.payload[0].tech,
                selectedTechEmail:action.payload[0].email,
                loading: false,
                month: action.payload[0].RowKey,
                errorMsg:'',
                reload:true
            };
        case "MOM_LOADING_DATA":
            return {
                ...state,
                loading: true,
                
                
            };
        case "MOM_DATA_RETRIEVED":
            return {
                ...state,
                loading:false,
                mom_tech_data:action.payload[0],
                mom_team_data:action.payload[1]
                
            };
        case "MOM_DATA_FAIL":
            return {
                ...state,
               mom_loading:false
            };
        case "Calls Taken":
            return {
                ...state,
                category:action.type,
                categorySortedData:SortCategory(state.chartData,action.type),
                categoryTerm: 'callsTaken',
                
                
            };
        case "Missed Calls":
            return {
                ...state,
                category:action.type,
                categorySortedData:SortCategory(state.chartData,action.type),
                categoryTerm: 'missedCalls',
               
            };
        case "Cases Opened":
            return {
                ...state,
                category:action.type,
                categorySortedData:SortCategory(state.chartData,action.type),
                categoryTerm: 'sn_openCase',
               
            };

        case "Task Opened":
            return {
                ...state,
                category:action.type,
                categorySortedData:SortCategory(state.chartData,action.type),
                categoryTerm: 'sn_openTask',
                
            };
        case "Total Opened":
            return {
                ...state,
                category:action.type,
                categorySortedData:SortCategory(state.chartData,action.type),
                categoryTerm: 'sn_openTotal',
                
            };
        case "Closed Cases":
            return {
                ...state,
                category:action.type,
                categorySortedData:SortCategory(state.chartData,action.type),
                categoryTerm: 'sn_closedCase',
               
            };
        case "Hero Points":
            return {
                ...state,
                category:action.type,
                categorySortedData:SortCategory(state.chartData,action.type),
                categoryTerm: 'heroPoints',
               
            };
        case "SLA":
            // "Doesnt do anything yet"
            return {
                ...state
            };
        default:
            return state
    }
};

export default Chart1Reducer